* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.news-loader {
  display: none;
  text-align: center;
}

.show {
  display: block;
}

.news-list {
  list-style: none;
  justify-content: center;
}


.news-list-li {
  /* outline: solid tomato; */
  display: flex;
  margin-bottom: 5px;
}
.name-country-big{
  padding-left: 10px;
  font-size: 34px;
  font-weight: 600;
}
.name-country{
  padding-left: 7px;
  font-size: 18px;
  font-weight: 600;
}
